"use client";
import { useState } from "react";
import Image from "next/image";

const navLinks = [
  {
    name: "ABOUT",
    href: "/",
    dropdown: [
      { name: "About JSS", href: "/" },
      { name: "Heritage", href: "/" },
    ],
  },
  {
    name: "ACADEMICS",
    href: "/",
    dropdown: [
      { name: "Schools", href: "/" },
      { name: "Departments", href: "/" },
      { name: "Programs", href: "/" },
    ],
  },
  {
    name: "ADMISSIONS",
    href: "/",
    dropdown: [
      { name: "Overview", href: "/" },
      { name: "Admission", href: "/" },
      { name: "UG Program", href: "/" },
    ],
  },
  {
    name: "FACILITIES",
    href: "/",
    dropdown: [
      { name: "Girls Hostel", href: "/" },
      { name: "Boys Hostel", href: "/" },
      { name: "Amenities Centre", href: "/" },
    ],
  },
  {
    name: "STUDENTS SUPPORT",
    href: "/",
    dropdown: [
      { name: "Student Life", href: "/" },
      { name: "Mentoring Scheme", href: "/" },
      { name: "Internal Complaint Committee", href: "/" },
    ],
  },
  {
    name: "EXAMINATION",
    href: "/",
    dropdown: [
      { name: "Code of Conduct", href: "/" },
      { name: "EMS- Portal link", href: "/" },
      { name: "Exam Regulations", href: "/" },
    ],
  },
];

export default function Navbar() {
  const [hoveredIndex, setHoveredIndex] = useState(null);

  return (
    <div>
      <nav className="navbar">
        <ul className="nav-links">
          {navLinks.map((link, index) => (
            <li
              key={link.name}
              className="nav-item"
              onMouseEnter={() => setHoveredIndex(index)}
              onMouseLeave={() => setHoveredIndex(null)}
            >
              <a href={link.href} className="nav-link">
                {link.name}
              </a>

              {link.dropdown && hoveredIndex === index && (
                <div className="dropdown">
                  <div className="dropdown-arrow"></div>
                  <ul>
                    {link.dropdown.map((item) => (
                      <li key={item.name}>
                        <a href={item.href}>{item.name}</a>
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </li>
          ))}
        </ul>
      </nav>

      <style jsx>{`
        .navbar {
          display: flex;
          background: #002147;
          padding: 15px 30px;
        }

        .nav-links {
          list-style: none;
          display: flex;
          gap: 30px;
        }

        .nav-item {
          position: relative;
        }

        .nav-link {
          color: white;
          text-decoration: none;
          font-size: 16px;
          padding: 8px 12px;
          display: block;
        }

        .nav-link:hover {
          background: #004080;
          border-radius: 4px;
        }

        /* Dropdown menu */
        .dropdown {
          position: absolute;
          top: 100%;
          left: 50%;
          transform: translateX(-50%);
          background: white;
          border-radius: 6px;
          margin-top: 10px;
          box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.15);
          min-width: 200px;
          z-index: 999;
          padding: 10px 0;
        }

        /* Arrow connecting nav item to dropdown */
        .dropdown-arrow {
          position: absolute;
          top: -8px;
          left: 50%;
          transform: translateX(-50%) rotate(45deg);
          width: 16px;
          height: 16px;
          background: white;
          box-shadow: -3px -3px 5px rgba(0, 0, 0, 0.05);
          z-index: -1;
        }

        .dropdown ul {
          list-style: none;
          margin: 0;
          padding: 0;
        }

        .dropdown li {
          padding: 10px 20px;
        }

        .dropdown li a {
          text-decoration: none;
          color: #002147;
          font-size: 15px;
          display: block;
          transition: background 0.2s;
        }

        .dropdown li a:hover {
          background: #f0f0f0;
        }

        /* Keep dropdown visible while hovering */
        .nav-item:hover .dropdown {
          display: block;
        }
      `}</style>
    </div>
  );
}
